body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
}

.App {
  position: relative;
  display: flex;
  flex-direction: column;
  font-size: 15px;
  color: black;  
  background-color: rgba(245, 245, 220, 0.842);
}

.header-img {
  height: 350px;
  width: 100%;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-bottom: 40px;
}

h1 {
  position: absolute;
  top: 70px;  
  font-family: arial, helvetica;
  color: white;
  text-shadow: 2px 2px 4px #000000;
  font-size: 35px;
  letter-spacing: .15em;
}

 .Date {
  display: flex;
  flex-direction: row;
  justify-content: center;
  position: absolute;
  top: 170px;
  width: 100%;
}

input[type="date"] {
  padding: 2px 8px;
  margin: 6px;
  color:rgba(0, 0, 0, 0.822);
  font-family: arial, helvetica;
  font-size: 15px;
  letter-spacing: .1em;
  border: 2px solid rgba(0, 0, 255, 0.363);
  border-radius: 4px;
  box-shadow: 10px 10px 5px rgba(128, 128, 128, 0.76);
}

p {
  position: absolute;
  top: 240px;
  margin: 0 auto;
  width: 100%;
  padding: 3px;
  color: rgba(62, 71, 80, 0.493);
  text-align: center;
}

input[type="text"] {
 height: 21px;
 width: 240px;
 margin-right: 3px;
 padding-left: 5px;
 padding-right: 5px;
 background-color: rgba(245, 245, 220, 0.918);
 border-width: 0px;
}

::placeholder {
  padding: 3px; 
  font-size: 15px;
  opacity: 0.7; 
  outline-color: rgb(168, 181, 185);
}

.add {
  width: 30px;
  height: 19.5px;
  display: inline-block;
  padding: 3px 2px 2px 6px;
  font-family: Arial, Helvetica, sans-serif;
  color: rgb(36, 34, 34);
  background-color: rgb(168, 181, 185);
  font-size: 14px;
  cursor: pointer;
}

.week-lists {
  top: 300px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: stretch;
  justify-content: space-evenly;
  width: 100%;
  height: 100%;
 }

.Day {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 320px;
  min-width: 330px;
  margin-bottom: 40px;
  background-image: linear-gradient(to top, #d8d1da, #daccdf, #dac7e5, #dac2ec, #d8bef3);
  box-shadow: 10px 10px 5px rgba(128, 128, 128, 0.61);
}

.day-name{
  width: 100%;
  margin: 16px 0px 5px 0px;
  border-bottom: 2px solid blanchedalmond;
  font-size: 21px;
  text-align: center;
  font-family: arial, helvetica;
  color: white;
}

ul {
  list-style-type: none;
  padding: 6px;
}

li {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 280px;
  min-height: 28px;
  background-color: beige;
  margin-bottom: 3px;
}

.trash-img {
  height: 20px;
  opacity: 0.8;
  margin-top: 2px;
  margin-left: 7px;
  cursor: pointer;
}

.done {
  opacity: 0.5;
  text-decoration: line-through;
  text-decoration-style: double;
  text-align: left;
}

.not-done {
  font-family: Arial, Helvetica, sans-serif;
  vertical-align: center;
  color: rgba(0, 0, 0, 0.89);
  margin-top: 2px;
  text-align: left;
}

.check-btn{
  min-width: 40px;
  height: 19px;
  margin-bottom: 3px;
  margin-right: 15px;
  padding: 3px 0px 5px 5px;
  background-color: rgb(145, 170, 179);
  color: rgb(56, 55, 55);
  font-size: 15px;
  box-shadow: 3px 3px 5px rgba(128, 128, 128, 0.699);
  cursor: pointer;
}

.summary {
  top: 90%;
  width: 100%;
  margin: 15px 0px;
  color: white;
  text-align: center;
}

